<style lang="scss" module>
.alert {
  margin: 22px 0 0;
}
.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.65);
  z-index: 9999;
  .modalContent {
    min-width: 260px;
    max-width: 400px;
    height: auto;
    margin: 10% auto;
    background: #fff;
    border-radius: 6px;
    padding: 12px;
    text-align: center;
    .modalIcon {
      font-size: 4em;
      color: #c9302c;
    }
  }
}
</style>

<template>
  <div class="component-container container-fluid">
    <div class="panel panel-default">
      <!-- title -->
      <div class="panel-heading">
        
        <router-link to="setting" class="pull-right" append replace>
          <span class="glyphicon glyphicon-cog"></span>
          
        </router-link>
      </div>

      <!-- Body -->
      <div class="panel-body">
        <!--  -->
        <div class="form-inline">
          <!--  -->
          <div class="form-group">
            <label></label>
            <input type="number" class="form-control" placeholder="User ID" min="1">
          </div>

          <!--  -->
          <div class="form-group">
            <label></label>
            <select class="form-control">
              <option></option>
              <option></option>
              <option></option>
              <option></option>
            </select>
          </div>

          <!--  -->
          <div class="form-group">
            <label></label>
            <select class="form-control">
              <option></option>
              <option></option>
            </select>
          </div>

          <!--  -->
          <button type="submit" class="btn btn-default"></button>

        </div>
      </div>

      <!--  -->
      <table v-show="cashes.length" class="table table-striped table-hover">
        <thead>
          <tr>
            <th>(ID)</th>
            <th>()</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="cash in cashes"
            :key="cash.id" :class="cash.status === 2 ? 'danger' : cash.status === 1 ? 'success' : ''"
          >
            <!--  -->
            <td>{{ cash.user.name }} ({{ cash.user.id }})</td>

            <!--  -->
            <td>{{ cash.value / 100 * ratio / 100 }} ({{ cash.value / 100 }})</td>

            <!--  -->
            <td v-if="cash.type === 'alipay'">{{ cash.account }}</td>
            <td v-else-if="cash.type === 'wechat'">{{ cash.account }}</td>
            <td v-else>{{ cash.account }}</td>

            <!--  -->
            <td v-if="cash.status === 1"></td>
            <td v-else-if="cash.status === 2"></td>
            <td v-else></td>

            <!--  -->
            <td v-if="actions[cash.id]">{{ remarks[cash.id] }}</td>
            <td v-else-if="cash.status === 0">
              <div class="input-group">
                <input type="text" class="form-control" placeholder="" v-model="remarks[cash.id]">
              </div>
            </td>
            <td v-else>{{ cash.remark }}</td>

            <!--  -->
            <td v-if="cash.status === 0">
              <!--  -->
              <button v-if="actions[cash.id] === 1" type="button" class="btn btn-primary btn-sm" disabled="disabled">
                <span class="glyphicon glyphicon-refresh component-loadding-icon"></span>
              </button>
              <button v-else-if="actions[cash.id] === 2" type="button" class="btn btn-primary btn-sm" disabled="disabled"></button>
              <button v-else type="button" class="btn btn-primary btn-sm" @click="requestCashPassed(cash.id)"></button>

              <!--  -->
              <button v-if="actions[cash.id] === 2" type="button" class="btn btn-danger btn-sm" disabled="disabled"></button>
              <button v-else-if="actions[cash.id] === 1" type="button" class="btn btn-danger btn-sm" disabled="disabled"></button>
              <button v-else type="button" class="btn btn-danger btn-sm"></button>
            </td>
            <td v-else></td>
          </tr>
        </tbody>
      </table>

      <!-- Loading -->
      <div v-if="loading" class="panel-body text-center">
        <span class="glyphicon glyphicon-refresh component-loadding-icon"></span>
        ...
      </div>

      <div v-else-if="alert.status" class="panel-body">
        <div :class="['alert', `alert-${alert.type}`, $style.alert]" role="alert">
          {{ alert.message }}
        </div>
        <br>
        <button type="button" class="btn btn-primary" @click="requestCashes"></button>
      </div>

    </div>

    <!--  -->
    <div class="text-center">
      <ul class="pagination">
        <li>
          <a href="#" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li><a href="#">1</a></li>
        <li><a href="#">2</a></li>
        <li><a href="#">3</a></li>
        <li><a href="#">4</a></li>
        <li><a href="#">5</a></li>
        <li>
          <a href="#" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </div>

    <!--  -->
    <div :class="$style.modal" v-show="modal.status">
      <div :class="$style.modalContent">
        <div :class="$style.modalIcon">
          <span v-if="modal.type"class="glyphicon glyphicon-ok-sign" style="color: #449d44;"></span>
          <span v-else class="glyphicon glyphicon-warning-sign"></span>
        </div>
        {{ modal.message }}
      </div>
    </div>

  </div>
</template>

<script>
import request, { createRequestURI } from '../../util/request';
import lodash from 'lodash';
export default {
  data: () => ({
    cashes: [],
    query: {},
    page: {
      last: 0,
      current: 1,
      first: 1,
    },
    alert: {
      status: false,
      message: '',
      type: 'info',
      interval: null,
    },
    loading: true,
    ratio: 100,
    actions: {},
    remarks: {},
    modal: {
      status: false,
      interval: null,
      type: false,
      message: '',
    }
  }),
  methods: {
    /**
     * 
     *
     * @param {Number} id
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    requestCashPassed(id) {
      // 
      const { [id]: remark } = this.remarks;
      if (! remark) {
        this.sendModal('', false);

        return;
      }

      // 
      this.actions = {
        ...this.actions,
        [id]: 1
      }

      // 
      request.patch(
        createRequestURI(`wallet/cashes/${id}`),
        { remark },
        { validateStatus: status => status === 201 }
      ).then(() => {
        this.actions = lodash.reduce(this.actions, function (actions, item, key) {
          if (parseInt(id) !== parseInt(key)) {
            actions[key] = item;
          }

          return actions;
        }, {});
        this.cashes = lodash.reduce(this.cashes, function (cashes, cash) {
          if (id === cash.id) {
            cash.remark = remark;
            cash.status = 1;
          }
          cashes.push(cash);

          return cashes;
        }, []);
        this.sendModal('');
      }).catch(({ response: { data: { remark = [], message = [] } = {} } = {} } = {}) => {
        const [ currentMessage = '' ] = [ ...remark, ...message ];
        this.actions = lodash.reduce(this.actions, function (actions, item, key) {
          if (parseInt(id) !== parseInt(key)) {
            console.log(id, key);
            actions[key] = item;
          }

          return actions;
        }, {});
        this.sendModal(currentMessage, false);
      });
    },

    /**
     * .
     *
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    requestCashes() {
      this.loading = true
      const query = { ...this.query, page: this.page.current };
      request.get(
        createRequestURI('wallet/cashes'),
        {
          query,
          validateStatus: status => status === 200
        }
      ).then(({ data = {} }) => {
        const {
          ratio = 100,
          cashes = [],
          current_page: current = this.page.current,
          first_page: first = this.page.first,
          last_page: last = thus.page.last,
        } = data;
        this.loading = false;
        this.cashes = cashes;
        this.page = { last, current, first };
        this.ratio = ratio;
      }).catch(({ response: { data: { message: [ message = '' ] = [] } = {} } = {} } = {}) => {
        this.loading = false;
        this.sendAlert('danger', message, false);
      });
    },

    /**
     * 
     *
     * @param {[type]} message [description]
     * @param {Boolean} success [description]
     * @return {[type]} [description]
     * @author Seven Du <shiweidu@outlook.com>
     */
    sendModal(message, success = true, time = 1500) {
      window.clearInterval(this.modal.interval);
      this.modal = {
        type: !!success,
        message,
        status: true,
        interval: window.setInterval(() => {
          this.modal.status = false;
          window.clearInterval(this.modal.interval);
        }, time)
      };
    },

    /**
     *  alert .
     *
     * @param {string} type
     * @param {string} message
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    sendAlert(type, message, hide = true) {
      window.clearInterval(this.alert.interval);
      this.loading = false;
      this.alert = {
        ...this.alert,
        type,
        message,
        status: true,
        interval: !hide ? null : window.setInterval(() => {
          window.clearInterval(this.alert.interval);
          this.alert.status = false;
        }, 2000)
      };
    }
  },
  created() {
    this.requestCashes();
  },
};
</script>



